@import '../GlobalStyles.scss';

.todoItem {
  padding: 20px;
  background-color: $dark-gray;
  border-radius: 8px;
  margin-top: 16px;
  justify-content: space-between;
  transition: all 0.4s;
  box-shadow: 0 0 8px 0px rgba(0, 0, 0, 0.3);

  &.active {
    border-left: $accent-color solid 1px;
  }

  &.isDone {
    opacity: 0.5;

    &:hover {
      opacity: 1;
    }
  }

  &:hover {
    transform: scale(1.01);
    box-shadow: 0 0 18px 0px rgba(0, 0, 0, 0.3);

    .todoText:not([disabled]) {
      transition: all 0.4s;
      color: $accent-color;

      .taskPlaceholder {
        color: $white;
      }
    }
  }

  .todoText {
    border: none;
    background: none;
    color: $white;
    font-family: $font-family;
    font-size: $body-font-size;
    cursor: pointer;
    overflow-wrap: break-word;
    text-align: left;
    max-width: 600px;
    width: 100%;

    .taskPlaceholder {
      opacity: 0.3;
    }

    &:disabled {
      cursor: not-allowed;
      opacity: 0.5;
      text-decoration: line-through;
    }
  }

  .date {
    font-size: $small-font-size;
    opacity: 0.5;
    margin-left: 16px;
  }
}
